
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Config from "../config";
export default {
components: {
XHeader,
XButton,
GroupTitle,
Group,
Cell,
CellBox,
XInput,
Divider,
Card,
Flexbox,
FlexboxItem,
},
data() {
return {




courseInfo: {
course: {
name: "",
course_place: "",
course_speaker: ""
},
course_time: {
start: "",
end: ""
}
},
inputArr: [
{
icon: "icon-xingming",
key: "姓名",
value: ""
},
{
icon: "icon-xingbie",
key: "性别:",
value: "man"
},
{
icon: "icon-dianhua",
key: "电话",
value: ""
},
{
icon: "icon-xuexiao",
key: "学校",
value: ""
},
{
icon: "icon-zhuanye",
key: "专业",
value: ""
},
{
icon: "icon-classes",
key: "班级",
value: ""
}
],
list: [{ key: "man", value: "男" }, { key: "woman", value: "女" }],
value: ""
};
},
mounted() {},
methods: {
signHandler: function() {
var querystring = require("querystring");
var url = Config.service.jobSignUrl,
signdata = {
user_name: this.inputArr[0].value,
user_sex: this.inputArr[1].value,
user_tel: this.inputArr[2].value,
user_school: this.inputArr[3].value,
user_special: this.inputArr[4].value,
user_classes: this.inputArr[5].value
};
this.$http.post(url, querystring.stringify(signdata)).then(
res => {
console.log(res.data);
this.$vux.alert.show({
title: "招募报名系统提醒您",
content: res.data.msg,
onShow() {
console.log("Plugin: I'm showing");
},
onHide() {
console.log("Plugin: I'm hiding now");
}
});
},
err => {

}
);
}
}
};



